
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transfor: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    transform: scaleY(0.5);
}
}
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
input {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  color: #7e8c8d;
  -webkit-backface-visibility: hidden;
  text-decoration: none;
}
li {
  list-style: none;
}
body {
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
@import "~common/stylus/iconfont.css";
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 50;
  height: 100%;
  width: 230px;
  background-color: #232a30;
  transform: translate3d(0, 0, 0);
}
.sidebar.fold-enter-active,
.sidebar.fold-leave-active {
  transition: all 0.5s;
}
.sidebar.fold-enter,
.sidebar.fold-leave-active {
  transform: translate3d(-100%, 0, 0);
}
.sidebar .user {
  padding: 10px 10px 0;
  font-size: 0;
}
.sidebar .user .avatar {
  display: inline-block;
  margin-right: 10px;
  width: 40px;
  height: 40px;
  background-image: url("useravatar.png");
  background-size: 40px 40px;
  background-repeat: no-repeat;
  border-radius: 50%;
  vertical-align: top;
}
.sidebar .user .username {
  display: inline-block;
  font-size: 15px;
  color: #94999d;
  line-height: 40px;
}
.sidebar .user .back {
  display: inline-block;
  vertical-align: top;
  position: relative;
  top: 10px;
  left: 60px;
  width: 30px;
  height: 30px;
}
.sidebar .menu-list {
  display: -ms-flexbox;
  display: flex;
  padding: 10px 20px 10px 10px;
}
.sidebar .menu-list .menu {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
}
.sidebar .menu-list .menu .avatar {
  margin-bottom: 5px;
}
.sidebar .menu-list .menu .avatar .iconfont {
  color: #94999d;
  font-size: 20px;
}
.sidebar .menu-list .menu .name {
  color: #94999d;
}
.sidebar .themes-list {
  position: absolute;
  top: 114px;
  bottom: 56px;
  width: 100%;
  overflow: hidden;
}
.sidebar .themes-list .themes {
  display: table;
  width: 100%;
  padding-left: 15px;
  color: #94999d;
}
.sidebar .themes-list .themes.current {
  background-color: #1b2329;
  color: #fff;
}
.sidebar .themes-list .themes .icons {
  display: inline-block;
  margin-right: 5px;
}
.sidebar .themes-list .themes .themetitle {
  display: inline-block;
  line-height: 50px;
}
.sidebar .bottom-menu {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 56px;
}
.sidebar .bottom-menu .menu {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
  padding-top: 5px;
}
.sidebar .bottom-menu .menu .avatar {
  margin-bottom: 5px;
}
.sidebar .bottom-menu .menu .avatar .iconfont {
  color: #94999d;
  font-size: 20px;
}
.sidebar .bottom-menu .menu .avatar img {
  margin-bottom: 2px;
}
.sidebar .bottom-menu .menu .name {
  color: #94999d;
}
.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 40;
  background: rgba(7,17,27,0.6);
  opacity: 1;
}
.mask.fade-enter-active,
.mask.fade-leave-active {
  transition: all 0.5s;
}
.mask.fade-enter,
.mask.fade-leave-active {
  opacity: 0;
}


